<script>
import axios from '../../node_modules/axios'
export default {
	data: function(){
		return {
			greeting: 'Contact me, gently and with purpose....',
			events: [],
			alertMessage: '',
			apiMessage: '',
			contName: '',
			contEmail: '',
			contSubject: '',
			contMessage: '',
			error: '',
			errors: []
		}
	},
	methods: {
		testAPI() {
			this.errors = []
			// Add token here for authentication
			// const token = ''

			let contName = document.getElementById('contName').value
			let contEmail = document.getElementById('contEmail').value
			let contSubject = document.getElementById('contSubject').value
			let contMessage = document.getElementById('contMessage').value

			if ( contName.length>4 && contEmail.length>4 && contSubject.length>4 && contMessage.length>4 ) {
				// Post data back to API for persistence. API code is also here on GitHub (Laravel58)
				const data = {
					details : {
						contName : contName,
						contEmail : contEmail,
						contSubject : contSubject,
						contMessage : contMessage
					}
				}

				const headers = {'Content-Type': 'application/x-www-form-urlencoded'}

				axios.post('https://www.codexobscurus.com/api/', data, headers)
					.then( this.greeting = 'Thank you for getting in touch.' )
					.then( this.apiMessage = 'If you left an email address or asked for contact I will be back in touch as soon as possible. Otherwise, thanks for getting in touch.' )
					.catch( error => ( this.errors.push( error ) ) )
			} else {
				this.errors.push('Please enter valid information in all fields. Thank you.')
			}

		},
		handleSubmit(e) {
			e.preventDefault()
			e.stopPropagation()
			this.testAPI()
		}
	}
}
</script>
<template>
	<div class="contact">
		<h2>{{ greeting }}</h2>
		<p>{{ alertMessage }}</p>

		<div v-if="apiMessage===''">

			<p v-if="errors.length">
				<b>Please correct the following error(s):</b>
				<ul>
					<li v-for="error in errors" v-bind:key="error.id">{{ error }}</li>
				</ul>
			</p>

			<form v-on:submit="handleSubmit">
				<div class="form-row">
					<div class="form-group col-md-6">
						<label for="contName">Name</label>
						<input type="text" class="form-control" id="contName" name="contName" placeholder="Name" required>
					</div>
				</div>

				<div class="form-row">
					<div class="form-group col-md-6">
						<label for="contEmail">Email</label>
						<input type="email" class="form-control" id="contEmail" name="contEmail" placeholder="Email" required>
					</div>
				</div>

				<div class="form-row">
					<div class="form-group col-md-6">
						<label for="contSubject">Subject</label>
						<input type="text" class="form-control" id="contSubject" name="contSubject" placeholder="Subject" required>
					</div>
				</div>

				<div class="form-row">
					<div class="form-group col-md-12">
						<label for="contMessage">Message</label>
						<textarea class="form-control" id="contMessage" name="contMessage" rows="3" placeholder="Message" required></textarea>
					</div>
				</div>

				<div class="form-row">
					<div class="form-group">
						<button type="submit" class="btn btn-primary">Submit</button>
					</div>
				</div>
			</form>
		</div>

		<div v-else>
			<p>{{apiMessage}}</p> 
		</div>

		<ul>
			<li v-for="event in events" v-bind:key="event.id">{{ eventText(event) }}</li>
		</ul>
	</div>
</template>
<style lang="scss" scoped>
    .testing {
        margin-top: 30px;
    }
</style>